<template>
  <v-container grid-list-xs>
    <v-overlay :value="overlayUnclosed">
      <v-progress-circular
          color="primary"
          indeterminate
          size="50"
      ></v-progress-circular>
    </v-overlay>
    <v-dialog
      transition="dialog-top-transition"
      max-width="600"
      v-model="confirmImportDialog"
    >
      <v-card>
        <v-toolbar
          color="primary"
          dark
        >About to import leave data</v-toolbar>
        <v-card-text>
          Are you sure that you want to import leave data into the system?
        </v-card-text>
        <v-card-actions class="justify-end">
          <v-btn @click="$router.go(-1)">Cancel</v-btn>
          <v-spacer></v-spacer>
          <v-btn @click="upload" color="error">Yes, Proceed</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-file-input
      v-model="file"
      accept=".xlsx"
      label="Upload Leave File"
    ></v-file-input>
    <v-btn color="success" @click="upload">Upload</v-btn>
  </v-container>
</template>
<script>
export default {
  props: ["queries"],
  data() {
    return {
      valid: true,
      overlayUnclosed: false,
      confirmImportDialog: false,
      file: ""
    }
  },
  methods: {
    upload() {
      this.overlayUnclosed = true
      const formData = new FormData();
      formData.append('leave', this.file);
      fetch("/site/practitioner/leaveImport", {
        method: "POST",
        body: formData
      }).then(response => response.blob().then((response) => {
        this.overlayUnclosed = false
        this.overlay
        this.$store.commit("setMessage", {
          type: "success",
          text: "Import is complete, check downloaded excel for a comprehensive report",
        });
        let blob = new Blob([response], { type: "application/vnd.ms-excel" });
        let link = document.createElement("a");
        link.href = window.URL.createObjectURL(blob);
        link.download = "importReport.xlsx";
        link.click();
        this.$router.push("/")
      }).catch(() => {
        this.overlayUnclosed = false
        this.$store.commit("setMessage", {
          type: "error",
          text: "An error has occured, try later",
        });
        return this.$router.go(-1)
      })).catch(() => {
        this.overlayUnclosed = false
        this.$store.commit("setMessage", {
          type: "error",
          text: "An error has occured, try later",
        });
        return this.$router.go(-1)
      })
    }
  }
}
</script>